<script>
export default {
  data() {
    return {
      th:[
        "Lanzamiento","Longitud estimada en",
        "Longitud real en","Longitud estimada-real"
      ],
      answers: {
        Estimacion:"",
        Longitud_Real:"",
        Diferencia:"",
        Nombres:"",
        Lanzamientos:[
          ["","",""],
          ["","",""],
          ["","",""],
        ],
      },
    };
  },
  methods: {
    getWord(i,k){
      if(i==1){
        return this.th[i-1] + " " + k
      }else{
        return this.th[i-1] + " " + this.getMed(k)
      }
    },
    getMed(k){
      if(k==1){
        return "cm."
      }else if(k==2){
        return "m."
      }else{
        return "km."
      }
    },
  }
};
</script>
